import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import crypt from 'crypto';
import { generateKeyPairSync } from 'crypto';
import elliptic from 'elliptic';
import { Container, Row, Col, Button, InputGroup, FormControl } from 'react-bootstrap';
const ellip = elliptic.ec;


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // currenthash: 0
    }
  }

  convertFromHex(hex) {
    var hex = hex.toString();//force conversion
    var str = '';
    for (var i = 0; i < hex.length; i += 2)
      str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
    return str;
  }

  convertToHex(str) {
    var hex = '';
    for (var i = 0; i < str.length; i++) {
      hex += '' + str.charCodeAt(i).toString(16);
    }
    return hex;
  }

  stringToBinary(input) {
    var characters = input.split('');

    return characters.map(function (char) {
      const binary = char.charCodeAt(0).toString(2)
      const pad = Math.max(8 - binary.length, 0);
      // Just to make sure it is 8 bits long.
      return '0'.repeat(pad) + binary;
    }).join('');
  }

  binaryToString(input) {
    let bytesLeft = input;
    let result = '';

    // Check if we have some bytes left
    while (bytesLeft.length) {
      // Get the first digits
      const byte = bytesLeft.substr(0, 8);
      bytesLeft = bytesLeft.substr(8);

      result += String.fromCharCode(parseInt(byte, 2));
    }

    return result;
  }


  generatePairs = () => {
    const base = Math.ceil(Math.random() * 193204900099096776)
    this.setState({ publickey: base })
    this.setState({ privatekey: Math.pow(base, 2) })
  }

  enCryptWithPublic = (message, publickey) => {
    const newmessage = this.convertToHex(message)
    this.setState({ currenthash: newmessage * Math.pow(publickey, 2) })
    // return this.convertToHex(message) * Math.tan(publickey)
  }

  deCryptWithPrivate = (encrypted, privatekey) => {
    console.log(encrypted)
    this.setState({ hiddenvalue: this.convertFromHex(encrypted / privatekey) })
    // return this.convertFromHex(encrypted) / privatekey
  }


  render() {
    return (
      <div className="App" className="App-header">
        <Container>
          <Row>
            <Col className='text-center'>
              <div>
                <h2>This key pair generator generates a 'FAB' keypair</h2>
                <h6>(So no SRA or others, also your name is Bob for now)</h6>
                <h6>'FAB' is a new mathematical algorithm which relates the public key to the private key, easy to crack</h6>
                <hr style={{ borderColor: 'white', width: '100px' }} />
                <br />
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button onClick={this.generatePairs} variant='outline-light'>Click to Generate 'FAB' KeyPair!</Button>
                  </InputGroup.Prepend>
                  <InputGroup.Prepend>
                    <Button variant='light'>Public key: </Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' placeholder='Share this with everyone' value={this.state.publickey} />
                  <InputGroup.Prepend>
                    <Button variant='dark'>private key: </Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' placeholder='Keep this secret!' value={this.state.privatekey} />
                </InputGroup>
              </div>
            </Col>
          </Row>
          <Row style={{ marginTop: '40px' }}>
            <Col md={6} sm={12}>
              <Col className='text-right' style={{ color: 'white', fontSize: '10px' }}>
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button variant='info'>Public key of receiver:</Button>
                  </InputGroup.Prepend>
                  <FormControl onChange={(e) => { this.setState({ currentpublickey: e.target.value }) }} className='input' placeholder='"64207642898649690" (Alice her key) ' />
                </InputGroup>
                <br />
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button variant='success'>Message:</Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' onChange={(e) => { this.enCryptWithPublic(e.target.value, this.state.currentpublickey) }} placeholder='666 (Only numbers)' />
                </InputGroup>
                <br />
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button variant='danger'>Encrypted message: </Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' value={this.state.currenthash} />
                </InputGroup>
              </Col>
            </Col>
            <Col md={6} sm={12}>
              <Col className='text-left' style={{ color: 'white', fontSize: '10px' }}>
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button variant='danger'>Encrypted message: </Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' onChange={(e) => { this.setState({ currenthashedvalue: e.target.value }) }} placeholder=' "-0.46262525705976"' />
                </InputGroup>
                <br />
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button variant='dark'>Private key:</Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' onChange={(e) => { this.deCryptWithPrivate(this.state.currenthashedvalue, e.target.value) }} placeholder='"0.4747631968048134" (Your private key)' />
                </InputGroup>
                <br />
                <InputGroup >
                  <InputGroup.Prepend>
                    <Button variant='success'>Decrypted message:</Button>
                  </InputGroup.Prepend>
                  <FormControl className='input' value={this.state.hiddenvalue} />
                </InputGroup>
              </Col>
            </Col>
          </Row>
          <Row>
            <Col>
              <br />
              <hr align='left' style={{ borderColor: 'white', width: '200px' }} />
              <h6>Rules:</h6>
              <ol>
                <li><h6>Only use simple words to <span style={{ color: 'red' }}>Encrypt</span> , some characters are forbidden which will gave 'NaN'</h6></li>
                <li><h6>Never share your private key</h6></li>
                <li><h6>Use the <span style={{ fontStyle: 'italic', color: 'lightblue' }}> Public key of the receiver</span> to <span style={{ color: 'red' }}>Encrypt</span> your message</h6></li>
                <li><h6>Send <span style={{ fontStyle: 'italic', color: 'lightblue' }}>Your own public key</span> for others to encrypt their message</h6></li>
                <li><h6>Use <span style={{ fontStyle: 'italic', color: 'lightblue' }}>Your own private key</span> to <span style={{ color: 'orange' }}>Decrypt</span> the <span style={{ color: 'red' }}>Encrypted</span> message</h6></li>
              </ol>
              <div className="text-right">
                <span style={{ fontSize: '13px' }}>Fabio de Bruijn INF2E</span>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default App;
